/* Container for top section */
.topPartBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fcc419;
  padding: 0 1rem;
  height: 140px;
  border-bottom: 4px solid #ffe600;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  position: relative;
}

/* Logo styling */
.homeLogo {
  height: 120px;
  width: auto;
  padding-top: 1rem;
  transition: transform 0.3s ease;
}

.homeLogo:hover {
  transform: scale(1.05);
}

/* Right-side icon group */
#LoginAndCartButtons {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* Reusable icon button base */
.cartLoginBt,
.searchIconButton {
  width: 4em;
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cartLoginBt button,
.searchIconButton {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s, border 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cartLoginBt button:hover,
.cartLoginBt button:focus,
.cartLoginBt button:active,
.searchIconButton:hover,
.searchIconButton:focus,
.searchIconButton:active {
  background-color: #fff9db;
  border: 2px solid #ffe600;
  outline: none;
}

/* Icon images inside buttons */
.cartLoginBt img,
.searchIconImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Popup overlay background */
.overlay-blur {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Dark overlay */
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Search popup container */
.search-popup {
  background: #fff9db;
  border: 2px solid #ffe600;
  border-radius: 12px;
  padding: 2rem;
  width: 90%;
  max-width: 500px;
  z-index: 100;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  text-align: center;
}

/* Optional blurred state for top bar */
.topPartBox.blurred {
  box-shadow: 0 0 10px rgba(0,0,0,0.4);
}

.cart-icon-wrapper {
  position: relative;
}

.cart-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  transform: translate(25%, -25%);
  background-color: red;
  color: white;
  font-size: 0.9rem;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 50%;
  z-index: 2;
}

/* Responsive layout changes */
@media (max-width: 960px) {
  .homeLogo {
    width: 80%;
  }

  #LoginAndCartButtons {
    gap: 1rem;
    padding-right: 1rem;
  }
}